.toggle_button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.toggle_button:focus {
    outline: none;
}

.toggle_button_line {
    width: 30px;
    height: 5px;
    background: white;
    margin-bottom: 5px;
}

.toggle_button:hover,
.toggle_button:active {
    background: #8C3041;
}  


