html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  background: -webkit-gradient(linear,  left top, right top,  from(rgb(136, 146, 211)), to(rgb(168, 218, 230)));
  background: -webkit-linear-gradient(left,  rgb(136, 146, 211), rgb(168, 218, 230));
  background: -o-linear-gradient(left,  rgb(136, 146, 211), rgb(168, 218, 230));
  background: linear-gradient(to right,  rgb(136, 146, 211), rgb(168, 218, 230));
}



